.menu{
    display: flex;
    background: $gray-300;
    position: relative;
}
.menu-level-1{
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0;
    width: 100%;
}
.menu-level-1 > li{
    height: 50px;
}
.menu-level-1 > li > a{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: $gray-900;
    padding: 0 20px;
}
.menu-level-1 > li > a > b{
    font-weight: 400;
}
.menu-level-2{
    list-style: none;
    padding: 0;
    display: none;
    position: absolute;
    z-index: 1;
    background: $white;
    left: 0;
    width: 100%;
}
.menu-level-1 > li:hover .menu-level-2{
    display: flex;
}
.menu-level-2 > li{
    padding: 0 20px;
    display: flex;
}
.menu-level-2 > li > a{

}
.menu-level-2 > li > div{

}
.menu-level-2 > li > div > a{

}
.menu-level-2 > li > div > a > span{

}

.menu-helper{
    display: none;
}